
.tmpl-vita {
  font-family: 'Open Sans', sans-serif;
  background: url("/static/background-gray.png") left/contain no-repeat;
  max-width: 1060px;
  width: 100%;
  margin: 0 auto;
}
.tmpl-vita a {
  color: unset;
  text-decoration: none;
  border-bottom: 0.5px solid #1ca9ca;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
.tmpl-vita a:hover {
  color: #1ca9ca;
  background-color: #e8e8ea;
}
.tmpl-vita header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: -10px;
  width: 100%;
  z-index: 1000;
  background-color: #fff;
  padding-bottom: 15px;
  border-bottom: 1px solid #e8e8ea;
}
.tmpl-vita header .logo {
  position: absolute;
  top: 60px;
  width: 80px;
  height: 80px;
  left: 0;
}
.tmpl-vita header .logo .i-logo {
  background: url("/static/logo.png") center/contain no-repeat;
  width: 80px;
  height: 80px;
}
.tmpl-vita header .text {
  padding: 40px 210px 40px 90px;
}
.tmpl-vita header .text .name {
  font-weight: bold;
  font-size: 3em;
}
.tmpl-vita header .text .title {
  font-weight: 100;
  text-transform: uppercase;
  font-size: 1.8em;
}
.tmpl-vita header .avatar {
  position: absolute;
  top: 25px;
  right: 0;
  height: 150px;
  width: 150px;
  overflow: hidden;
  border-radius: 35px;
}
.tmpl-vita h2 {
  text-transform: uppercase;
  color: #1ca9ca;
  position: relative;
  padding-left: 2em;
}
.tmpl-vita h2 .fa {
  position: absolute;
  top: 0.2em;
  left: 0;
  width: 1.5em;
  height: 1.5em;
  vertical-align: middle;
}
.tmpl-vita summary {
  padding-left: 3em;
  padding-right: 3em;
}
.tmpl-vita .tech,
.tmpl-vita .exp,
.tmpl-vita .edu,
.tmpl-vita .sns {
  padding-top: 15px;
}
.tmpl-vita .tech .logo,
.tmpl-vita .exp .logo,
.tmpl-vita .edu .logo,
.tmpl-vita .sns .logo {
  width: 200px;
  height: 50px;
}
.tmpl-vita .tech .logo img,
.tmpl-vita .exp .logo img,
.tmpl-vita .edu .logo img,
.tmpl-vita .sns .logo img {
  width: 100%;
}
.tmpl-vita .tech .exp-item .col-3,
.tmpl-vita .exp .exp-item .col-3,
.tmpl-vita .edu .exp-item .col-3,
.tmpl-vita .sns .exp-item .col-3 {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-left: 3em;
}
.tmpl-vita .tech .exp-item .col-3 .institute,
.tmpl-vita .exp .exp-item .col-3 .institute,
.tmpl-vita .edu .exp-item .col-3 .institute,
.tmpl-vita .sns .exp-item .col-3 .institute {
  position: relative;
  font-size: 1.4em;
  font-weight: bold;
}
.tmpl-vita .tech .exp-item .col-3 .institute i,
.tmpl-vita .exp .exp-item .col-3 .institute i,
.tmpl-vita .edu .exp-item .col-3 .institute i,
.tmpl-vita .sns .exp-item .col-3 .institute i {
  color: #d2d2d2;
  position: absolute;
  top: 5px;
  right: -10px;
  z-index: 100;
}
.tmpl-vita .tech .exp-item .col-3 .institute.active i,
.tmpl-vita .exp .exp-item .col-3 .institute.active i,
.tmpl-vita .edu .exp-item .col-3 .institute.active i,
.tmpl-vita .sns .exp-item .col-3 .institute.active i {
  color: #1ca9ca;
}
.tmpl-vita .tech .exp-item .col-3 .duration,
.tmpl-vita .exp .exp-item .col-3 .duration,
.tmpl-vita .edu .exp-item .col-3 .duration,
.tmpl-vita .sns .exp-item .col-3 .duration {
  line-height: 1.5em;
  font-size: 1.2em;
  font-style: italic;
  font-weight: 100;
  color: #203630;
}
.tmpl-vita .tech .exp-item .col-9,
.tmpl-vita .exp .exp-item .col-9,
.tmpl-vita .edu .exp-item .col-9,
.tmpl-vita .sns .exp-item .col-9 {
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-left: 2px solid #e8e8ea;
  padding-left: 3.6em;
  padding-bottom: 1em;
}
.tmpl-vita .tech .exp-item .col-9 .position,
.tmpl-vita .exp .exp-item .col-9 .position,
.tmpl-vita .edu .exp-item .col-9 .position,
.tmpl-vita .sns .exp-item .col-9 .position {
  font-size: 1.2em;
}
.tmpl-vita .tech .exp-item .col-9 .items,
.tmpl-vita .exp .exp-item .col-9 .items,
.tmpl-vita .edu .exp-item .col-9 .items,
.tmpl-vita .sns .exp-item .col-9 .items {
  color: #203630;
  line-height: 1.4em;
}
.tmpl-vita .tech .exp-item .col-9 .logo,
.tmpl-vita .exp .exp-item .col-9 .logo,
.tmpl-vita .edu .exp-item .col-9 .logo,
.tmpl-vita .sns .exp-item .col-9 .logo {
  position: absolute;
  top: 0;
  right: 0;
  width: 100px;
  height: 100px;
  overflow: hidden;
  opacity: 0.5;
}
.tmpl-vita .tech .exp-item .col-9 .logo img,
.tmpl-vita .exp .exp-item .col-9 .logo img,
.tmpl-vita .edu .exp-item .col-9 .logo img,
.tmpl-vita .sns .exp-item .col-9 .logo img {
  width: 100%;
}
.tmpl-vita .tech .duration,
.tmpl-vita .exp .duration,
.tmpl-vita .edu .duration,
.tmpl-vita .sns .duration {
  text-transform: capitalize;
}
.tmpl-vita .sns {
  padding: 20px 0;
}
.tmpl-vita .sns .exp-item .fa {
  margin-left: 20px;
  margin-right: 20px;
  padding: 10px 5px;
  color: #1ca9ca;
}
.tmpl-vita .sns .exp-item a {
  border: none;
  display: inline-block;
}


