
html,
body {
  margin: 0;
  padding: 0;
}
.ellipsis {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.row {
  width: 100%;
}
.row:after {
  content: '';
  visibility: hidden;
  display: block;
  height: 0;
  clear: both;
}
.row .col {
  float: left;
}
.clearfix {
  clear: both;
}
.col-1 {
  width: 8.333333333333332%;
}
.col-2 {
  width: 16.666666666666664%;
}
.col-3 {
  width: 25%;
}
.col-4 {
  width: 33.33333333333333%;
}
.col-5 {
  width: 41.666666666666664%;
}
.col-6 {
  width: 50%;
}
.col-7 {
  width: 58.33333333333333%;
}
.col-8 {
  width: 66.66666666666666%;
}
.col-9 {
  width: 75%;
}
.col-10 {
  width: 83.33333333333333%;
}
.col-11 {
  width: 91.66666666666666%;
}
.col-12 {
  width: 100%;
}
.i {
  display: inline-block;
}
.tmpl {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
main {
  padding-bottom: 20px;
}
.t-center {
  text-align: center;
}
@media screen and (max-width: 640px) {
.tmpl {
    padding-left: 20px;
    padding-right: 20px;
}
}


